import React from 'react';
import { LinkFicha } from '../../components/Links/LinkFicha';
import AlignmentChart from '../../assets/alignment-chart.png'
import ExperienceTable from '../../assets/experience-table.png'
import StatsTable from '../../assets/stats-table.png'
import { LinkLivroJogador } from '../../components/Links/LinkLivroJogador';
import './TutorialFicha.css'

function TutorialFicha() {
  return (
    <div>
        <section className='downloads'>
            <label>Voc pode baixar a ficha aqui: <LinkFicha /></label>
        </section>

        <section className='sheet-header'>
            <p className='area'>Vamos comear pelo cabealho da ficha</p>
            <ul>
                <li>
                    <p>Nome do Personagem: Mais autoexplicativo impossvel</p>
                </li>
                <li>
                    <p>Classe e nvel: A classe  a profisso do seu personagem podendo ser escolhido entre ''classes'' e o nvel ser importante para sabermos quais habilidades dessa classe seu personagem j desbloqueou </p>
                    <p className='ref-book'>A lista de classes vai da pgina 46  121 no <LinkLivroJogador />, mas eventualmente eu vou escrever aqui</p>
                </li>
                <li>
                    <p>Raa: Representa a raa do seu personagem, podendo ser escolhido dentre: ''raas'', no tem regras pra escolha de raa, mas cada raa oferece suas caractersticas ao personagem</p>
                    <p className='ref-book'>A lista de raas vai da pgina 17  43 no <LinkLivroJogador />, mas eu vou escrever aqui tbm</p>
                </li>
                <li>
                    <p>Antecedente: Toda histria tem um incio. Os antecedentes de seus personagens revela de onde eles vieram, como se tornaram aventureiros e seu lugar no mundo. Escolher um antecedente fornece a voc importantes pistas sobre a identidade de seu personagem. A questo mais importante a ser respondida pelo seu antecedente  o que mudou? Por que voc parou de fazer algo do seu antecedente e comeou a se aventurar? Aonde voc conseguiu recursos para comprar seu equipamento inicial, ou, caso voc possua um antecedente que indique riqueza, porque voc no tem mais os recursos? Como voc aprendeu as percias que compem sua classe? O que o separa das pessoas ordinrias que compartilham do seu mesmo antecedente?</p>
                    <p>Os exemplos de antecedentes do livro concedem a voc tanto benefcios concretos (caractersticas, proficincias e idiomas), quanto sugestes de interpretao.</p>
                    <p className='ref-book'>A lista de antecedentes est na pgina 127 do <LinkLivroJogador /></p>
                </li>
                <li>
                    <p className='text-top'>Alinhamento: <img src={AlignmentChart} className="alignment-img" alt="diagrama de alinhamentos" /></p>
                </li>
                <li>
                    <p>Nome do jogador: Pra ningum roubar o personagem do colega</p>
                </li>
                <li>
                    <p>Pontos de experincia: Esse conceito  novo ein</p>
                    <p className='ref-book'>Pgina 15 tem a tabela no <LinkLivroJogador /> mas tem aqui tbm</p>
                    <img src={ExperienceTable} className="alignment-img" alt="tabela de experiencia" />
                </li>
            </ul>
        </section>

        <section className='sheet-status'>
            <p className='area'>Agora vamos preencher os status, aqui temos duas maneiras de prosseguir</p>
            <ul>
                <li>
                    <p>Jogando dados: Voc joga seis vezes (uma para cada status) 4d6 e tira o menor nmero, aquele  o seu valor no status </p>
                </li>
                <li>
                    <p>Comprando status: A critrio do Mestre, voc pode usar esta variao para determinar seus valores de habilidade. O mtodo descrito aqui permite que voc crie um personagem com um conjunto de valores de habilidade escolhido individualmente. Voc tem 27 pontos para gastar em seus valores de habilidade. O custo de cada valor de habilidade  mostrado na tabela Custo de Pontos de Habilidades. Por exemplo, um valor 14 custa 7 pontos. Usando este mtodo, 15 ser o valor de habilidade mais alto que voc pode conseguir inicialmente, antes de aplicar as melhorias raciais.
                    <br />Voc no pode ter uma habilidade menor que 8.</p>
                    <p className='ref-book'>a tabela t na pgina 13 do <LinkLivroJogador /></p>
                    <img src={StatsTable} className="alignment-img" alt="tabela de Status" />
                </li>
            </ul>

        </section>
    </div>
  );
}

export default TutorialFicha;
