            "description": "Voc pronuncia uma palavra de comando para uma criatura que voc possa ver dentro do alcance. O alvo deve ser bem sucedido num teste de resistncia de Sabedoria ou seguir seu comando no prximo turno dele. A magia no tem efeito se o alvo for um morto-vivo, se ele no entender seu idioma ou se o comando for diretamente nocivo a ele."
