        "higherLevels": "Em Nveis Superiores. Se voc conjurar essa magia usando um espao de magia de 2 nvel ou superior, voc pode afetar uma criatura adicional para cada nvel do espao acima do 1. As criaturas devem estar a 9 metros [30 feet] entre si para serem afetadas."
