              "Voc concede a at dez criaturas que voc possa ver, imunidade a uma nica magia ou outro efeito mgico por 8 horas. Por exemplo, voc poderia deixar voc e todos os seus companheiros imunes ao ataque de dreno de vida de um lich.",
              "Voc desfaz um nico evento recente forando uma nova jogada de qualquer jogada feita na ltima rodada (incluindo seu ltimo turno). A realidade remodela-se para acomodar o novo resultado. Por exemplo, uma magia desejo poderia desfazer o teste de resistncia bem sucedido de um oponente, um acerto crtico de um inimigo ou o teste de resistncia fracassado de um amigo."
