            "type": "default",
            "description": "Voc pode forar que a nova jogada seja feita com vantagem ou desvantagem e voc pode escolher se ir usar o resultado da nova jogada ou da jogada original. Voc  capaz de fazer coisas alm do alcance dos exemplos acima. Apresente seu desejo ao Mestre o mais precisamente possvel. O Mestre tem grande amplitude em definir o que ocorre em tais circunstncias; quanto maior o desejo, maior ser a possibilidade de que algo d errado. Essa magia pode simplesmente falhar, o efeito do seu desejo pode ser apenas parcialmente atendido ou voc pode sofrer consequncias imprevistas como resultado da forma que voc formulou o desejo. Por exemplo, desejar que um vilo esteja morto pode impulsionar voc para um perodo no tempo em que o vilo no esteja mais vivo, efetivamente removendo voc do jogo. Similarmente, desejar um item mgico lendrio ou um artefato poderia, instantaneamente, transportar voc para a presena do dono atual do item."
