            "type": "default",
            "description": "O estresse da conjurao dessa magia para produzir qualquer efeito diferente de copiar outra magia enfraquece voc. Aps enfrentar esse estresse, a cada vez que voc conjurar uma magia, antes de terminar um descanso longo, voc sofrer 1d10 de dano necrtico por nvel da magia. Esse dano no pode ser reduzido ou prevenido de forma alguma. Alm disso, sua Fora cai para 3, se ela j no for 3 ou inferior, por 2d4 dias. Para cada dia desses que voc permanecer descansando e no fizer nada alm de atividades leves, seu tempo de recuperao  reduzido em 2 dias. Finalmente, existe 33 por cento de chance de voc se tornar incapaz de conjurar desejo novamente se voc sofrer esse estresse."
