            "description": "Voc tenta seduzir uma besta que voc possa ver dentro do alcance. Ela deve ser bem sucedida num teste de resistncia de Sabedoria ou ficar enfeitiada por voc pela durao. Se voc ou criaturas amigveis a voc estiverem lutando com ela, ela ter vantagem no teste de resistncia."
