      "body": {
        "description": [
          {
            "type": "default",
            "description": "Quando voc conjura essa magia, voc inscreve um glifo que fere outras criaturas, tanto sobre uma superfcie (como uma mesa ou uma seco de piso ou parede) quanto dentro de um objeto que possa ser fechado (como um livro, um pergaminho ou um ba de tesouro) para ocultar o glifo. Se voc escolher uma superfcie, o glifo pode cobrir uma rea da superfcie no superior a 3 metros [10 feet] de dimetro. Se voc escolher um objeto, o objeto deve permanecer no seu local; se ele for movido mais de 3 metros [10 feet] de onde voc conjurou essa magia, o glifo ser quebrado e a magia termina sem ser ativada."
          },
          {
            "type": "default",
            "description": "O glifo  quase invisvel e requer um teste de Inteligncia (Investigao) contra a CD da magia para ser encontrado."
          },
          {
            "type": "default",
            "description": "Voc define o que ativa o glifo quando voc conjura a magia. Para glifos inscritos em uma superfcie, os gatilhos mais tpicos incluem tocar ou ficar sobre o glifo, remover outro objeto cobrindo o glifo, aproximar-se a uma certa distncia do glifo ou manipular um objeto onde o glifo esteja inscrito. Para glifos inscritos dentro de objetos, os gatilhos mais comuns incluem abrir o objeto, aproximar-se a uma certa distncia do objeto ou ver ou ler o glifo. Uma vez que o glifo seja ativado, a magia termina."
          },
          {
            "type": "default",
            "description": "Voc pode, posteriormente, aperfeioar o gatilho para que a magia se ative apenas sob certas circunstncias ou de acordo com as caractersticas fsicas (como altura ou peso), tipo de criatura (por exemplo, a proteo poderia ser definida para afetar aberraes ou drow) ou tendncia. Voc pode, tambm, definir condies para criaturas no ativarem o glifo, como aqueles que falarem determinada senha."
          },
          {
            "type": "default",
            "description": "Quando voc inscreve o glifo, escolha runas explosivas ou glifo de magia."
          },
          {
            "type": "option",
            "value": "Runas Explosivas",
            "description": "Runas Explosivas. Quando ativado, o glifo irrompe com energia mgica numa esfera com 6 metros de raio [20-foot radius], centrada no glifo. A esfera se espalha, dobrando esquinas. Cada criatura na rea deve realizar um teste de resistncia de Destreza. Uma criatura sofre 5d8 de dano de cido, eltrico, fogo, frio ou trovejante se falhar no teste de resistncia (voc escolhe o tipo quando cria o glifo) ou metade desse dano se obtiver sucesso."
          },
          {
            "type": "option",
            "value": "Glifo de Magia",
            "description": "Glifo de Magia. Voc pode armazenar uma magia preparada de 3 nvel ou inferior no glifo ao conjura-la como parte da criao do glifo. A magia a ser armazenada no tem efeito imediato quando conjurada dessa forma. Quando o glifo for ativado, a magia armazenada  conjurada. Se a magia tiver um alvo, esse alvo ser a criatura que ativou o glifo. Se a magia afetar uma rea, a rea ser centrada na criatura. Se a magia invocar criaturas hostis ou criar objetos ou armadilhas nocivos, eles aparecero o mais prximo possvel do intruso e o atacaro. Se a magia precisar de concentrao, ela dura o mximo possvel da sua durao."
          }
        ],
        "higherLevels": "Em Nveis Superiores. Quando voc conjurar essa magia usando um espao de magia de 4 nvel ou superior, o dano do glifo de runas explosivas aumenta em 1d8 para cada nvel do espao acima do 3. Se voc criar um glifo de magia, voc pode armazenar qualquer magia do mesmo nvel, ou inferior, do espao que voc usar para o glifo de vigilncia."
      },
