            "description": "Voc impe um comando mgico a uma criatura que voc possa ver, dentro do alcance, forando-a a fazer algum servio ou reprimindo-a por alguma ao ou curso de atividade, como voc decidir. Se a criatura puder compreender voc, ela deve ser bem sucedida num teste de resistncia de Sabedoria ou ficar enfeitiada por voc pela durao. Enquanto a criatura estiver enfeitiada por voc, ela sofrer 5d6 de dano psquico toda vez que ela agir de maneira diretamente contrria s suas instrues, mas no mais de uma vez por dia. Uma criatura que no puder compreender voc no  afetada por essa magia."
