      "body": {
        "description": [
          {
            "type": "default",
            "description": "Uma muralha no-mgica de rocha slida surge do nada num ponto,  sua escolha, dentro do alcance. A muralha tem 15 centmetros [6 inches] de espessura e  composta por dez painis de 3 metros [10 feet] por 3 metros [10 feet]. Cada painel deve ser contguo com, pelo menos, outro painel. Alternativamente, voc pode criar painis de 3 metros [10 feet] por 6 metros [20 feet] com apenas 7,5 centmetros [3 inches] de espessura."
          },
          {
            "type": "default",
            "description": "Se a muralha passar pelo espao ocupado por uma criatura quando ela surgir, a criatura ser empurrada para um dos lados da muralha (voc escolhe qual lado). Se a criatura fosse ser rodeada por todos os lados da muralha (ou pela muralha e outra superfcie slida), a criatura pode realizar um teste de resistncia de Destreza. Se obtiver sucesso, ela pode usar sua reao para se mover at seu deslocamento, assim no ficando mais cercada pela muralha."
          },
          {
            "type": "default",
            "description": "A muralha pode ter qualquer formato que voc desejar, no entanto, ela no pode ocupar o mesmo espao de uma criatura ou objeto. A muralha no precisa ser vertical ou se apoiar em qualquer fundao estvel. Ela deve, no entanto, se fundir e estar solidamente suportada por rocha existente. Ento, voc pode usar essa magia para criar uma ponte sobre um abismo ou criar uma rampa."
          },
          {
            "type": "default",
            "description": "Se voc criar um vo com mais de 6 metros [20 feet] de comprimento, voc deve reduzir o tamanho de cada painel  metade para criar suportes. Voc pode moldar grosseiramente a parede para criar merles, ameias e assim por diante."
          },
          {
            "type": "default",
            "description": "A muralha  um objeto feito de pedra que pode ser danificado e ento, partido. Cada painel tem CA 15 e 30 pontos de vida para cada 2,5 centmetros [1 inch] de espessura. Reduzir os pontos de vida de um painel a 0, o destruir e pode fazer painis conectados desmoronarem,  critrio do Mestre."
          },
          {
            "type": "default",
            "description": "Se voc mantiver sua concentrao nessa magia por toda a durao, a muralha se tornar permanente e no poder ser dissipada. Do contrrio, a muralha desaparece quando a magia acabar."
          }
        ]
      },
