            "description": "Voc cria uma defesa contra viagem mgica que protege at 3.500 metros quadrados [40.000 square feet] de solo at 9 metros [30 feet] de altura do solo. Pela durao, criaturas no conseguem se teletransportar para dentro da rea ou usar portais, como os criados pela magia portal, para entrar na rea. A magia protege a rea contra viagem planar e, portanto, impede criaturas de acessarem a rea por meio do Plano Astra, Plano Etreo, Faria, Umbra ou pela magia viagem planar."
