            "type": "default",
            "description": "Alm disso, a magia causa dano a certos tipos de criatura,  sua escolha, quando a conjurar. Escolha um ou mais dentre os seguintes: celestiais, corruptores, elementais, fadas ou mortos-vivos. Quando uma criatura escolhida entrar na rea da magia pela primeira vez em um turno ou comea seu turno nela, a criatura sofre 5d6 de dano radiante ou necrtico ( sua escolha, quando voc conjura a magia)."
