            "description": "Voc cria uma defesa que protege at 225 metros quadrados [2.500 square feet] de espao (uma rea de um quadrado de 15 metros [50 feet] ou cem quadrados de 1,5 metro [5 feet] ou vinte e cinco quadrados de 3 metros [10 feet]). A rea protegida pode ter at 6 metros de altura [20-foot-tall], no formado que voc desejar. Voc pode proteger diversos armazns de uma fortaleza dividindo a rea entre eles, contanto que voc possa andar em cada rea contgua enquanto estiver conjurando a magia."
