            "description": "Corredores. Nvoa preenche todos os corredores protegidos, tornando-os rea de escurido densa. Alm disso, cada interseo ou passagem ramificada oferecendo uma escolha de direo, h 50 por cento de chance de uma criatura diferente de voc acredite que est indo na direo oposta  que escolheu."
