            "description": "Voc modela uma duplicata ilusria de uma besta ou humanoide, dentro do alcance, durante todo tempo de conjurao da magia. A duplicada  uma criatura, parcialmente real, formada de gelo ou neve e pode realizar aes e, no mais, ser tratada como uma criatura normal. Ela aparenta ser igual a original, mas tem metade do mximo de pontos de vida da criatura e  formada sem qualquer equipamento. No mais, a iluso usa todas as estatsticas da criatura que ela copiou."
