            "description": "Cada alvo deve realizar um teste de resistncia de Sabedoria. Se falhar na resistncia, ele seguir o curso de ao que voc descreveu, da melhor forma possvel. O curso de ao sugerido pode continuar por toda a durao. Por exemplo, voc poderia sugeria a um grupo de soldados que deem todo o seu dinheiro para o primeiro mendigo que encontrarem. Se a condio no for atingida antes da magia acabar, a atividade no  realizada."
